.u_ab-center {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%); }
.bg-black {
	background: #000; }
.bg-red {
	background: $primary-red-color; }
.bg-red-2 {
	background: $primary-red-2-color; }
.bg-yellow-gradient {
	background: $yellow-gradient; }
.bg-cover {
	background-size: cover;
	background-position: center; }

.whitePlaneCover {
	position: absolute;
	z-index: 1;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: #fff;
	transform: scale(0); }
.img-res {
	max-width: 100%; }

.clearfix:before,
.clearfix:after {
	content: " " {
  	display: table; } }
.clearfix:after {
	clear: both; }

.opacity-0 {
	opacity: 0; }
.pointer-events-none {
	pointer-events: none; }
.pointer-events-auto {
	pointer-events: auto; }

.rounded-15 {
	border-radius: 15px!important; }


.rounded-primary {
	border-radius: $radius; }



